//=== header ==
.header {
  background-image: url("../images/nois-wallpaper-abstract.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;

  .navigation {
    background-color: $white;

    .navbar {
      padding: 0;

      .container {

        .navbar-toggler:focus, .navbar-toggler:active {
