    position: relative;

    .heading {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      text-align: center;

      h1 {
        //margin-bottom: 3em;
        text-align: center;
        background: linear-gradient(330deg, #38c695 0%, #b27cf5 25%, #feb960 50%, #fc5f45 75%, #e05252 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }

      .features {
        .price {
