//=====carousel========

.main {
  .carouselSlider {
    .container {
      .bd-example {
        .carousel {
          @include md {
            padding-bottom: 2em;
          }

          .carousel-inner {
            @include sm {
              padding: 3em 1em;
            }
            @include md {
              padding: 1em;
            }
            @include lg {
              padding: 0 1em;
            }

            .carousel-item {
              @include sm {
                //display: block;
                padding: 5em 0;
              }

              img {
                @include sm {
                  width: 40%;
                  height: auto;
                }
                @include md {
                  width: 50%;
                }
                @include lg {
                  width: auto;
                  height: auto;
                }
              }

              .carousel-caption {
                @include xs {
                  padding: 2em;
                }
                @include sm {
                  padding: 1em 1em 0 1.5em;
                  position: absolute;
                  top: -5%;
                  right: 0;
                  left: 39%;
                  bottom: -5%;
                }
                @include md {
                  padding: 2em;
                  top: 0;
                  right: 0;
                  left: 50%;
                  bottom: 0;
                }
                @include lg {
                  padding: 6em 1em 1em 3em;
                  top: 1em;
                  right: 0;
                  left: 40%;
                  bottom: 1em;
                }
                @include xl {
                  top: 2em;
                  right: 0;
                  left: 34%;
                  bottom: 2em;
                }

                p {
                  @include sm {
                    line-height: 1.4;
                  }
                  @include md {
                    line-height: 1.8;
                  }
                  @include lg {
                    line-height: 1.8;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

